import React from "react"
import Link from "gatsby-link"
import hobbits from "./images/race_hobbit.jpg"
import elves from "./images/race_woodelf.jpg"
import dwarves from "./images/race_dwarf.jpg"
import humans from "./images/race_human.jpg"
import beornings from "./images/race_beorning.jpg"
import urukhais from "./images/race_urukhai.jpg"
import orcs from "./images/race_orc.jpg"
import ologs from "./images/race_ologhai.jpg"
import lhuths from "./images/race_uruklhuth.jpg"
import harads from "./images/race_haradrim.jpg"



export default ({ data }) => 
<div>
    <div className="jumbotron">
        <div className="container">
            <h1>Welcome to Return of the Shadow!</h1>
            <p>Return of the Shadow is a game based on the Fourth Age of Arda, J.R.R. Tolkien's wonderful world of fantasy. Game play takes place after the destruction of the One Ring. After Sauron's fall, a period of peace was laid about the land, and the Elves, Humans, Dwarves, and Hobbits prospered.</p>
            <p><Link className="btn btn-primary btn-lg" to="/connect" role="button">Start Playing &raquo;</Link></p>
        </div>
    </div>
    <section className="news-preview">
        <div className="container">
            <div className="row">
                <h1 className="title">News</h1>
            </div>
            {data.allMarkdownRemark.edges.map(({ node }) =>
                <div key={node.id} className="col-md-4">
                    <h2>{node.frontmatter.title}</h2>
                    <p>{node.excerpt}</p>
                    <p><Link className="btn btn-primary" to={node.frontmatter.path} role="button">View Details &raquo;</Link></p>
                </div>
            )}
        </div>
    </section>
    <section className="races offwhite">
        <div className="container">
            <div className="row">
                <h1 className="title">Races</h1>
            </div>
            <div className="row">
                <h2>Good Races</h2>
                <div className="col-lg-4">
                    <div className="card">
                        <img src={hobbits} className="card-img-top"/>
                        <div className="card-content">
                            <h3 className="card-title">Hobbits</h3>
                            <p className="card-text">Also known as Halflings, are an ancient mortal race that live in Middle-Earth. Although their exact...</p>
                            <Link to="/races/hobbits/" className="btn btn-primary">View Details &raquo;</Link>
                        </div>
                    </div>
                </div>
                <div className="col-lg-4">
                    <div className="card">
                        <img src={elves} className="card-img-top"/>
                        <div className="card-content">
                            <h3 className="card-title">Wood Elves</h3>
                            <p className="card-text">Also known as Silvan, are mainly Nandor in descent but also mixed with Avari. Many of them were...</p>
                            <Link to="/races/woodelves/" className="btn btn-primary">View Details &raquo;</Link>
                        </div>
                    </div>
                </div>
                <div className="col-lg-4">
                    <div className="card">
                        <img src={dwarves} className="card-img-top"/>
                        <div className="card-content">
                            <h3 className="card-title">Dwarves</h3>
                            <p className="card-text">The Dwarves were made by Aulë, whom they themselves call Mahal, meaning "maker." Aulë was...</p>
                            <Link to="/races/dwarves/" className="btn btn-primary">View Details &raquo;</Link>
                        </div>
                    </div>
                </div>
                <div className="col-lg-4">
                    <div className="card">
                        <img src={humans} className="card-img-top"/>
                        <div className="card-content">
                            <h3 className="card-title">Humans</h3>
                            <p className="card-text"></p>
                            <Link to="/races/humans/" className="btn btn-primary">View Details &raquo;</Link>
                        </div>
                    </div>
                </div>
                <div className="col-lg-4">
                    <div className="card">
                        <img src={beornings} className="card-img-top"/>
                        <div className="card-content">
                            <h3 className="card-title">Beornings</h3>
                            <p className="card-text"></p>
                            <Link to="/races/beornings/" className="btn btn-primary">View Details &raquo;</Link>
                        </div>
                    </div>
                </div>
            </div>
            <div className="row">
                <h2>Evil Races</h2>
                <div className="col-lg-4">
                    <div className="card">
                        <img src={urukhais} className="card-img-top"/>
                        <div className="card-content">
                            <h3 className="card-title">Uruk-Hais</h3>
                            <p className="card-text"></p>
                            <Link to="/races/uruk-hais/" className="btn btn-primary">View Details &raquo;</Link>
                        </div>
                    </div>
                </div>
                <div className="col-lg-4">
                    <div className="card">
                        <img src={orcs} className="card-img-top"/>
                        <div className="card-content">
                            <h3 className="card-title">Common Orcs</h3>
                            <p className="card-text"></p>
                            <Link to="/races/orcs/" className="btn btn-primary">View Details &raquo;</Link>
                        </div>
                    </div>
                </div>
                <div className="col-lg-4">
                    <div className="card">
                        <img src={ologs} className="card-img-top"/>
                        <div className="card-content">
                            <h3 className="card-title">Olog-Hais</h3>
                            <p className="card-text"></p>
                            <Link to="/races/olog-hais/" className="btn btn-primary">View Details &raquo;</Link>
                        </div>
                    </div>
                </div>
                <div className="col-lg-4">
                    <div className="card">
                        <img src={lhuths} className="card-img-top"/>
                        <div className="card-content">
                            <h3 className="card-title">Uruk-Lhuths</h3>
                            <p className="card-text"></p>
                            <Link to="/races/uruk-lhuths/" className="btn btn-primary">View Details &raquo;</Link>
                        </div>
                    </div>
                </div>
                <div className="col-lg-4">
                    <div className="card">
                        <img src={harads} className="card-img-top"/>
                        <div className="card-content">
                            <h3 className="card-title">Haradrims</h3>
                            <p className="card-text"></p>
                            <Link to="/races/haradrims/" className="btn btn-primary">View Details &raquo;</Link>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
    <section className="classes">
        <div className="container">
            <div className="row">
                <h1 className="title">Classes</h1>
                <div className="col-lg-6">
                    <h2>Ranger</h2>
                </div>
                <div className="col-lg-6">
                    <h2>Warrior</h2>
                </div>
                <div className="col-lg-6">
                    <h2>Mage</h2>
                </div>
                <div className="col-lg-6">
                    <h2>Mystic</h2>
                </div>
            </div>
        </div>
    </section>
</div>


export const query = graphql `
query IndexQuery {
    allMarkdownRemark(limit: 6, sort: {fields: [frontmatter___date], order: DESC}) {
        edges {
            node {
                id
                frontmatter {
                    title
                    date(formatString: "DD MMMM, YYYY")
                    path
                }
                excerpt
            }
        }
    }
}
`


